  export const ConvertByte = (size: number): string => {
    const kb = 1024;
    const mb = Math.pow(kb, 2);
    const gb = Math.pow(kb, 3);
    const tb = Math.pow(kb, 4);
    let target = null;
    let unit = 'byte';
    if (size >= tb) {
      target = tb;
      unit = 'TB';
    } else if (size >= gb) {
      target = gb;
      unit = 'GB';
    } else if (size >= mb) {
      target = mb;
      unit = 'MB';
    } else if (size >= kb) {
      target = kb;
      unit = 'KB';
    }
    const res = target !== null ? Math.floor((size / target) * 100) / 100 : size;
    return `${res}  ${unit}`;
  };

